<template>
  <div>
    <h2 style="background: #ccc; text-align:center;" ref="h2">{{status? '': ''}}</h2>
    <pre ref="cont" @dblclick="stop">
      import axios from 'axios';
      import Cookies from 'js-cookie';
      import { Message } from 'element-ui';
      import router from './../router/index';
      axios.defaults.timeout = 10000;

      var init = {                                                [...]
        // 
        timer: null,
        // refresh_token
        isRefresh: false,
        //                                                            
        openMessage: function(msg) {                                    
          Message({                                                 
            message: msg,                                        
            type: 'error',                                       
            showClose: true,                                      
          });                                                      
        },                                                           
        getRefreshToken: function() {                                  
          let params = {                                                 
            refresh_token: Cookies.get('refresh_token'),                   
          };                                                                 
          let that = this;                                                     
          axios({
            method: 'post',
            url: `api/v1/refresh_token${params}`,
          })
            .then(function(res) {
              if (res.data.access_token) {
                // refresh_token
                that.isRefresh = false;
                let result = res.data;
                let millisecond = new Date().getTime();
                let expiresTime = result.expires_in * 1000;
                let utilTime = millisecond + expiresTime;
                Cookies.set('access_token', result.access_token, { expires: expiresTime });
                Cookies.set('utilTime', utilTime);
                Cookies.set('refresh_token', result.refresh_token);
                Cookies.set('expires_in', result.expires_in);
              } else {
                //token
                localStorage.clear();
                Cookies.remove('access_token');
                Cookies.remove('utilTime');
                Cookies.remove('expires_in');
                Cookies.remove('refresh_token');
                router.replace({
                  path: '/login',
                  query: { redirect: router.currentRoute.fullPath },
                });
              }
            })
            .catch(function(err) {
              //token
              get_sys_logout();
              localStorage.clear();
              Cookies.remove('access_token');
              Cookies.remove('utilTime');
              Cookies.remove('expires_in');
              Cookies.remove('refresh_token');
              console.log(err);
              that.openMessage('');
              router.replace({
                path: '/login',
                query: { redirect: router.currentRoute.fullPath },
              });
            });
        },
      };

      //http request 
      axios.interceptors.request.use(
        config => {
          config.data = JSON.stringify(config.data);
          init.timer = new Date().getTime();
          if (Cookies.get('access_token')) {
            <!-- if ((parseInt(Cookies.get('utilTime')) - init.timer) / (1000 * 60 * 60) < 0) { -->
              Cookies.remove('access_token');
              Cookies.remove('utilTime');
              Cookies.remove('expires_in');
              Cookies.remove('refresh_token');
              get_sys_logout();
              localStorage.clear();
              router.replace({
                path: '/login',
                query: { redirect: router.currentRoute.fullPath },
              });
            }
            config.headers = {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${Cookies.get('access_token')}`,
            };
          } else {
            config.headers = {
              'Content-Type': 'application/json',
            };
          }
          return config;
        },
        error => {
          Message.error({
            message: '',
          });
          return Promise.reject(error);
        }
      );

      //
      axios.interceptors.response.use(
        response => {
          if (Cookies.get('utilTime')) {
            if (!init.isRefresh) {
              // 30
              <!-- if ((parseInt(Cookies.get('utilTime')) - init.timer) / (1000 * 60 * 60) < 0.5) { -->
                init.isRefresh = true;
                init.getRefreshToken();
              }
            }
          }
          return response;
        },
        err => {
          // debugger
          if (err && err.response) {
            switch (err.response.status) {
              case 400:
                console.log('');
                break;
              case 401:
                //token
                get_sys_logout();
                localStorage.clear();
                Cookies.remove('access_token');
                Cookies.remove('expires_in');
                Cookies.remove('utilTime');
                Cookies.remove('refresh_token');
                init.openMessage('');
                router.replace({
                  path: '/login',
                  query: { redirect: router.currentRoute.fullPath },
                });
                break;
              case 403:
                init.openMessage('');
                break;
              case 404:
                init.openMessage(',');
                break;
              case 405:
                init.openMessage('');
                break;
              case 408:
                init.openMessage('');
                break;
              case 500:
                init.openMessage('');
                break;
              case 501:
                init.openMessage('');
                break;
              case 502:
                init.openMessage('');
                break;
              case 503:
                init.openMessage('');
                break;
              case 504:
                init.openMessage('');
                break;
              case 505:
                init.openMessage('http');
                break;
              default:
                init.openMessage(`${err.response.status}`);
            }
          } else {
            init.openMessage('');
          }
          return Promise.reject(err.response);
        }
      );
      export default axios;

    </pre>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "axios",
  data() {
    return {
      rgb: "",
      status: true,
      timer: ""
    };
  },
  mounted() {
    this.timer = setInterval(this.change, 1000);
  },
  methods: {
    change() {
      console.log(this.$refs.cont);
      this.bg3();
      this.$refs.cont.style.color = this.rgb;
      this.$refs.h2.style.color = this.rgb;
    },
    stop() {
      if (this.status) {
        clearInterval(this.timer);
        this.status = false;
      } else {
        this.status = true;
        this.timer = setInterval(this.change, 1000);
      }
    },
    bg3() {
      var r = Math.floor(Math.random() * 256);
      var g = Math.floor(Math.random() * 256);
      var b = Math.floor(Math.random() * 256);
      this.rgb = "rgb(" + r + "," + g + "," + b + ")"; //ES6`{$}`
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<style scoped>
pre {
  background: #000;
  color: rgb(28, 143, 5);
  font-size: 2em;
  cursor: pointer;
}
.test {
  height: 100vh;
  width: 100vw;
  font-size: 2em;
}
</style>




