<template>
  <div>
    <el-button type="primary" @click="display = !display">{{display?'close': 'open'}}</el-button>
    <drawer title="" :display.sync="display" :width="drawerWidth" :height="drawerHeight">
 <el-collapse v-model="activeName" accordion>
  <el-collapse-item title=" Consistency" name="1">
    <div></div>
    <div></div>
  </el-collapse-item>
  <el-collapse-item title=" Feedback" name="2">
    <div></div>
    <div></div>
  </el-collapse-item>
  <el-collapse-item title=" Efficiency" name="3">
    <div></div>
    <div></div>
    <div></div>
  </el-collapse-item>
  <el-collapse-item title=" Controllability" name="4">
    <div></div>
    <div></div>
  </el-collapse-item>
</el-collapse>
    </drawer>
  </div>
</template>

<script>
import drawer from './../pages/drawer';
export default {
  components: { drawer },
  data() {
    return {
      activeName: '1',
      display: true,
      drawerWidth: '100%',
      drawerHeight: '500px',
    };
  },
};
</script>

<style>
</style>



