<template>
  <div>
    <el-form :model="form" class="demo-form-inline" label-width="220px">
      <el-form-item label="">
        <el-input v-model="form.user" placeholder=""></el-input>===>
        <h3>{{form.user}}</h3>
      </el-form-item>
      <el-form-item label="-_-" prop="name">
        <el-input
          :value="form.name"
          @input="e => form.name = validSe(e)"
          maxlength="10"
          placeholder="10"
        ></el-input>===>
        <h3>{{form.name}}</h3>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.region" placeholder="">
          <el-option label="" value="shanghai"></el-option>
          <el-option label="" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
/* eslint-disable */
export default {
  data() {
    return {
      form: {
        user: "",
        name: "",
        region: ""
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>
<style lang="less" scoped>
.demo-form-inline {
  // width: 500px;
  h3 {
    display: inline-block;
  }
  .el-input {
    width: 250px;
  }
}
</style>




