/* eslint-disable */
const list = {
  data() {
    return {
      loading: false,
      pageParam: {
        pageNum: 1, // 
        pageSize: 10, // 
      },
      total: 0, // 
      pageSizes: [10, 20, 30, 50], // 
      pageLayout: 'total, sizes, prev, pager, next, jumper', // 
      pagerCount: 5, //  5  21 
      list: [],
    };
  },
  methods: {
    // 
    handleSizeChange(val) {
      this.pageParam.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.pageParam.pageNum = val;
      this.getList();
    },
    /**
     * 
     * @param {*} apiResult
     * @returns {*} promise
     */
    listSuccessCb(apiResult = {}) {
      return new Promise((resolve, reject) => {
        let tempList = []; // resolve returnlist
        try {
          this.loading = false;
          tempList = apiResult.data.data.list;
          this.total = apiResult.data.totalCount;
          // 
          resolve(tempList);
        } catch (error) {
          reject(error);
        }
      });
    },
    /**
     * 
     * loading false
     */
    listExceptionCb(error) {
      this.loading = false;
      console.error(error);
    },
  },
  created() {
    this.$nextTick().then(() => {
      console.log('table mixin')
    });
  },
};
export default list;



