<template>
  <div>
     <el-form-item label="" label-width="120px">
        <el-upload class="upload-demo" action="" :auto-upload="false" :limit="1" ref="upload" :http-request="upload" multiple>
          <el-button size="small" type="primary">2</el-button>
        </el-upload>
      </el-form-item>
 <el-button class="btn" size="small" type="primary" @click="upload">2</el-button>

    <p>{{detailContent}}</p>
    <!-- -->
    <el-upload
      class="avatar-uploader"
      :action="serverUrl"
      name="img"
      :headers="header"
      :show-file-list="false"
      :on-success="uploadSuccess"
      :on-error="uploadError"
      :before-upload="beforeUpload"
    ></el-upload>
    <!---->
    <el-row v-loading="quillUpdateImg">
      <quill-editor
        v-model="detailContent"
        ref="myQuillEditor"
        :options="editorOption"
        @change="onEditorChange($event)"
        @ready="onEditorReady($event)"
      ></quill-editor>
    </el-row>
  </div>
</template>
<script>
/* eslint-disable */
// 
const toolbarOptions = [
  ['bold', 'italic', 'underline', 'strike'], // toggled buttons
  ['blockquote', 'code-block'],

  [{ header: 1 }, { header: 2 }], // custom button values
  [{ list: 'ordered' }, { list: 'bullet' }],
  [{ script: 'sub' }, { script: 'super' }], // superscript/subscript
  [{ indent: '-1' }, { indent: '+1' }], // outdent/indent
  [{ direction: 'rtl' }], // text direction

  [{ size: ['small', false, 'large', 'huge'] }], // custom dropdown
  [{ header: [1, 2, 3, 4, 5, 6, false] }],

  [{ color: [] }, { background: [] }], // dropdown with defaults from theme
  [{ font: [] }],
  [{ align: [] }],
  ['link', 'image', 'video'],
  ['clean'], // remove formatting button
];
export default {
  data() {
    return {
      quillUpdateImg: false,
      serverUrl: 'http://127.0.0.1:3000/dopost', // 
      header: { token: sessionStorage.token }, // token
      detailContent: '', // 
      editorOption: {
        placeholder: '',
        theme: 'snow', // or 'bubble'
        modules: {
          toolbar: {
            container: toolbarOptions, // 
            handlers: {
              image: function(value) {
                if (value) {
                  // alert(1)
                  document.querySelector('.avatar-uploader input').click();
                  // document.querySelector('#quill-upload input').click();
                } else {
                  this.quill.format('image', false);
                }
              },
            },
          },
        },
      },
    };
  },
  methods: {
    upload() {
      const formData = new FormData()
      const file = this.$refs.upload;
      const headerConfig = { headers: { 'Content-Type': 'multipart/form-data' }}
      if (!file) { // 
        alert('')
        return
      }
      formData.append('file', file.raw)
      axios.post('http://localhost:3000/upload', formData, headerConfig).then(res => {
        console.log(res)
      })
    },

    onEditorReady(quill) {
      console.log('editor ready!', quill);
    },
    onEditorChange({ quill, html, text }) {
      console.log('editor change!', quill, html, text);
      this.detailContent = html;
    },
    // 
    beforeUpload() {
      // loading
      this.quillUpdateImg = true;
    },
    uploadSuccess(res, file) {
      // res
      // 
      let quill = this.$refs.myQuillEditor.quill;
      // 
      if (res.code === '200' && res.info !== null) {
        // 
        let length = quill.getSelection().index;
        //   res.info
        quill.insertEmbed(length, 'image', res.info);
        // 
        quill.setSelection(length + 1);
      } else {
        this.$message.error('');
      }
      // loading
      this.quillUpdateImg = false;
    },
    // 
    uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error('');
    },
  },
};
</script>



